 .contact {
     width: 280px;
     margin: 0 auto;
     padding-bottom: 100px;
 }

 .contact-box {
     background-color: #7E6D53;
     border-radius: 8px;
     display: flex;
     flex-direction: column;
     align-items: center;
     background-repeat: no-repeat;
     background-position: center;
     background-size: cover;
 }

 .contact-title {
     color: #7E6D53;
     font-family: Poppins;
     font-size: 28px;
     font-style: normal;
     font-weight: 500;
     line-height: normal;
     text-align: center;
 }

 .contact-text {
     color: #7E6D53;
     font-family: Poppins;
     text-align: center;
     font-size: 20px;
     font-style: normal;
     font-weight: 300;
     line-height: normal;
     margin-bottom: 26px;
 }

 .contact-icon {
     fill: #FCFCFB;
 }


 .contact-item-text {
     color: #FFF;
     font-family: Poppins;
     font-size: 14px;
     font-style: normal;
     font-weight: 300;
     line-height: normal;
 }

 .contact-list {
     display: flex;
     flex-direction: column;
     gap: 14px;
     padding: 24px;
 }

 .contact-item {
     display: flex;
     gap: 8px;
 }

 .contact-us {
     color: #2D2A22;
     padding: 14px 76px;
     font-family: Poppins;
     font-size: 16px;
     font-style: normal;
     font-weight: 300;
     line-height: normal;
     background: #FCFCFB;
     border-radius: 8px;
 }

 @media screen and (min-width: 480px) {}

 @media screen and (min-width: 768px) {
     .contact {
         width: 704px;
         margin: 0 auto;
     }

     .contact-title {
         font-size: 40px;
     }

     .contact-text {
         font-size: 24px;
     }

     .contact-list {
         gap: 24px;
     }

     .contact-box {
         display: flex;
         flex-direction: row;
         gap: 68px;
         justify-content: center;
         padding: 53px 74px;
     }

     .contact-us {
         padding: 12px 42px;
     }
 }


 @media screen and (min-width: 1280px) {
     .contact-box {
         gap: 64px;
     }
 }