.infrastructure { 
    display: flex; 
    flex-direction: column; 
    font-family: Poppins, sans-serif;
   
    font-family: Poppins, sans-serif;
   
  } 
   
  .infrastructure-title { 
    color: var(--text-of-pages, #7e6d53); 
    font-size: 28px;
    font-size: 28px;
    font-weight: 500; 
    align-self: start; 
  } 
   

  .infrastructure-list { 
    position: absolute; 
    top: 0;
    border-radius: 8px; 
    top: 40px;
    background-color: rgba(252, 252, 251, 0.8); 
    display: flex; 
    margin-top: -4px; 
    height: 351px;
    flex-direction: column; 
    padding-top: 0px;
    padding:  32 ;
  } 
   

 .infrastructure-background {
    background-image: url(/src/images/mobile-img/infrastructure.webp);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: relative;
  height: 431px;
  display: flex;
  justify-content: center;
 }
   

  .infrastructure-circletext { 
    border-radius: 50%; 
    border: 1px solid var(--button-text, #2d2a22); 
    overflow: hidden; 
    color: var(--button-text, #2d2a22); 
    font-size: 14px;
    font-weight: 600; 
    width: 101px; 
    height: 101px; 
    padding: 0 17px; 
    display: flex; 
    align-items: center; 
    justify-content: center; 
  } 
   
  .infrastructure-text { 
    color: var(--text-of-pages, #7e6d53); 
    font-size: 14px;
    font-weight: 500; 
    flex-grow: 1; 
    margin: auto 0; 
  } 

   @media screen and (min-width: 320px) {}

   @media screen and (min-width: 768px) {
    .infrastructure-background {
      background-image: url(/src/images/tablet-img/infrastructure.webp);
      height: 486px;
    }
    .infrastructure-list {
      width: 331px;
      height: 470px;
      right: 37px;
      top: 8px;
    }
    .infrastructure-title {
      font-size: 40px;
    }
    .infrastructure-circletext {
      font-size: 16px;
    }
    .infrastructure-text {
      font-size: 16px;
      font-weight: 400px;
    }
    .infrastructure-item:not(:first-child) {
      margin-top: 60px; 
    }

   }

   @media screen and (min-width: 1200px) {
    .infrastructure-background {
      background-image: url(/src/images/desktop-img/infrastructure.webp);
      height: 486px;
    }

    .infrastructure {
      display: flex;
    }

    .infrastructure-title {
      font-size: 40px;
    }
    .infrastructure-list {
      width: 331px;
      height: 470px;
      right: 186px;
    }
    .infrastructure-circletext {
      font-size: 16px;
    }
    .infrastructure-text {
      font-size: 16px;
      font-weight: 400px;
    }

   }

  .infrastructure-item { 
    display: flex; 
    gap: 8px; 
  } 
   
  
