.philosophy {
  background-image: url(../images/mobile-img/mobile-philosophy.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 677px;
  margin-top: 100px;
}

.philosophy-box2 {
  opacity: 0.9;
  background: #FCFCFB;
  height: 398px;
}

.philosophy-card1 {
  margin-right: 20px;
}

.philosophy-card-text {
  color: #7E6D53;
  font-family: Poppins;
  font-size: 16px;
  font-weight: 400;
}

.philosophy-card-list {
  display: flex;
  flex-wrap: wrap;
  padding-top: 20px;
}

.philosophy-card-item {
  padding-right: 20px;
  padding-bottom: 20px;
  width: calc((100% - 20px * 2) / 2);
}

.philosophy-icon {
  width: 32px;
  height: 32px;
  margin-bottom: 20px;
}

.philosophy-title {
  color: #2D2A22;
  font-size: 16px;
  font-weight: 300;
}

.philosophy-text {
  color: #7E6D53;
  font-size: 12px;
  font-weight: 300;
}

@media screen and (min-width: 320px) {}

@media screen and (min-width: 768px) {
  .philosophy {
    background-image: url(../images/tablet-img/philosophy.webp);
  }

  .philosophy-box2 {
    display: flex;
    padding-top: 40px;
  }

  .philosophy-title {
    font-size: 24px;
  }

  .philosophy-text {
    font-size: 16px;
    width: 336px;
  }
}

@media screen and (min-width: 1200px) {
  .philosophy {
    background-image: url(../images/desktop-img/philosophy.webp);
  }

  .philosophy-card-list {
    padding-left: 134px;
  }

  .philosophy-card-item {
    padding-right: 72px;
  }

  .philosophy-title {
    padding-top: 60px;
  }

}